#add-new {
    width: 220px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #ca453b;
    color: white;
    margin-top: 5px;
    height: 30px;
}

#add-new:hover {
    cursor: pointer;
    opacity: 0.7;
}

.catalog-scroll {
    position: fixed;
    margin-top: 0px;
    z-index: 1;
    left: 10px;
    height: calc(100% - 117px);
    overflow-y: auto;
    width: 240px;
}

#catalog-container {
    display: flex;
    flex-direction: column;
